	import config from '@/common/config.js';
	const phoneReg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
	
    export default {
        data() {
            return {
                phone: '',
                yzm: '',
                positionTop: 0,
				txt:'获取验证码',
				isDisabled: false
            }
        },
        methods: {
            initPosition() {
                /**
                 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
                 * 反向使用 top 进行定位，可以避免此问题。
                 */
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
			bindGetYzm(){
				var _this = this;
				if (_this.phone && phoneReg.test(_this.phone)) {
					uni.request({
						url: config.urlArr.baseUrl + 'sms/send', 
						method: 'POST',
						data: {
							mobile: _this.phone,
							event: 'mobilelogin'
						},
						success: (res) => {
							console.log(JSON.stringify(res));
							if(res.data.code == 1){
								let num = 20;
								_this.isDisabled = true;
								let timer = setInterval(function(){
									if(num > 1){
										num--;
										_this.txt = num+'s后重新获取'
									}else{
										clearInterval(timer);
										_this.isDisabled = false;
										_this.txt = '获取验证码'
									}
								},1000)
							}else{
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
						},
						fail: (err) =>{
							console.log(JSON.stringify(err))
							uni.showToast({
								title: err.errMsg,
								icon:'none'
							});
						}
					}); 
				}else{
					uni.showToast({
					    icon: 'none',
					    title: '请重新输入手机号'
					});
				}
			},
            bindLogin() {
                if (!phoneReg.test(this.phone)) {
                    uni.showToast({
                        icon: 'none',
                        title: '请重新输入手机号'
                    });
                    return;
                }
                if (!this.yzm) {
                    uni.showToast({
                        icon: 'none',
                        title: '请重新输入验证码'
                    });
                    return;
                }
				uni.request({
					url: config.urlArr.baseUrl + 'user/mobilelogin', 
					method: 'POST',
					data: {
						mobile: this.phone,
						captcha: this.yzm
					},
					success: (res) => {
						if(res.data.code == 1){
							uni.showToast({
								title: '登录成功',
								icon: 'none'
							})
							try {
								uni.setStorageSync('userInfo', JSON.stringify(res.data.data.userinfo));
								uni.setStorageSync('token', res.data.data.userinfo.token);
								uni.reLaunch({
									url:"../ucenter/ucenter"
								})
							} catch (e) {
								// error
							}
						}else if(res.data.code == 0){
							uni.showToast({
								title: res.data.msg,
								icon: 'none'
							})
						}
					},
					fail: (err) =>{
						console.log(JSON.stringify(err));
					}
				});
            }
        },
        onReady() {
            this.initPosition();
        }
    }
